import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import "./NavBar.css";

import logo from "../images/logo.jpg";
function Navbar() {
  const [click, setClick] = useState(false);

  const handleClick = () => setClick(!click);

  return (
    <>
      
      <nav className="navbar">
        <div className="nav-container">
          <NavLink exact to="/" className="nav-logo">
            <img src={logo} alt="" width="150" height="65" />
          </NavLink>

          <ul className={click ? "nav-menu active" : "nav-menu"}>
            <li className="nav-item">
              <NavLink
                exact
                to="/home"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
                Home
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/about-us"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
                About
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/services"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
                Service
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/approvals"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
                Approvals
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                exact
                to="/contact-us"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
                Contact Us
              </NavLink>
            </li>

            {/* <li className="nav-item">
              <a href="tel: 9930652916">
                <button
                  type="button"
                  class="btn btn-outline-secondary"
                  style={{
                    color: "rgb(0, 195, 255)",
                    border: "1.5px solid  rgb(0, 195, 255)",
                  }}
                >
                  <strong>Call</strong>
                </button>{" "}
              </a>
            </li>
            <li className="nav-item">
              <a href="mailto: info@ixarrobotic.com">
                <button
                  type="button"
                  class="btn btn-outline-secondary"
                  style={{
                    color: "rgb(0, 195, 255)",
                    border: "1.5px solid  rgb(0, 195, 255)",
                  }}
                >
                  <strong>Email</strong>
                </button>
              </a>
            </li>

            <li className="nav-itemb">
              <NavLink
                exact
                to="/Partner"
                activeClassName="active"
                className="nav-links"
                onClick={handleClick}
              >
                <button
                  type="button"
                  class="btn btn-outline-secondary"
                  style={{
                    color: "rgb(0, 195, 255)",
                    border: "1.5px solid  rgb(0, 195, 255)",
                  }}
                >
                  <strong>Partner With Us</strong>
                </button>
              </NavLink>
            </li> */}
          </ul>
          <div className="nav-icon" onClick={handleClick}>
            <i className={click ? "fas fa-times" : "fas fa-bars"}></i>
          </div>
        </div>
      </nav>
    </>
  );
}

export default Navbar;
